import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css'
import InfiniteScroll from 'react-infinite-scroller'
// import './popular.less'

class App extends Component {
    constructor() {
        super()
        this.state = {
            list: [],
            infinitKey: 'All',//keypage
            url: {
                'All': 'https://api.github.com/search/repositories?q=stars:3E1&sort=stars&order=desc&type=Repositories',
                'JavaScript': 'https://api.github.com/search/repositories?q=stars:3E1+language:javascript&sort=stars&order=desc&type=Repositories',
                'Ruby': 'https://api.github.com/search/repositories?q=stars:3E1+language:ruby&sort=stars&order=desc&type=Repositories',
                'Java': 'https://api.github.com/search/repositories?q=stars:3E1+language:java&sort=stars&order=desc&type=Repositories',
                'CSS': 'https://api.github.com/search/repositories?q=stars:3E1+language:css&sort=stars&order=desc&type=Repositories'
            },
            hasMore: true, // 
            pageUrl: 'https://api.github.com/search/repositories?q=stars:3E1&sort=stars&order=desc&type=Repositories',
            isLoading: false,//
        }
        
    }
    componentDidMount(){
        this.loadMoreData(1)
    }
    // 
    loadMoreData = (page = 1) => {
        const { pageUrl } = this.state
        this.setState({ isLoading: true, hasMore: false }, ()=>{
            this.getData(`${pageUrl}&page=${page}`)
        })
    }
    //
    getData = (url = 'https://api.github.com/search/repositories?q=stars:3E1&sort=stars&order=desc&type=Repositories') => {
        let { list } = this.state
        fetch(url).then(res => res.json())
            .then(res => {
                if (res.items) {
                    this.setState({ list: [...list, ...res.items], hasMore:true })
                }
            }).catch(e => {
                console.log(e)  
            }).finally(()=>{
                this.setState({ isLoading: false })
            })
        
    }
    //
    onSwitch = str => {

        this.setState({ list: [], pageUrl: this.state.url[str], infinitKey: str, isLoading: true }, () => { this.loadMoreData(1) })
    }
    render() {
        const { list, hasMore, infinitKey } = this.state
        return (
            <div className='a'>
                <div style={styles.nav}>
                    <div onClick={() => { this.onSwitch('All') }} style={{ cursor: 'pointer', margin: '0 20px', color: infinitKey === 'All' ? 'red' : '' }}>All</div>
                    <div onClick={() => { this.onSwitch('JavaScript') }} style={{ cursor: 'pointer', margin: '0 20px', color: infinitKey === 'JavaScript' ? 'red' : '' }}>JavaScript</div>
                    <div onClick={() => { this.onSwitch('Ruby') }} style={{ cursor: 'pointer', margin: '0 20px', color: infinitKey === 'Ruby' ? 'red' : '' }}>Ruby</div>
                    <div onClick={() => { this.onSwitch('Java') }} style={{ cursor: 'pointer', margin: '0 20px', color: infinitKey === 'Java' ? 'red' : '' }}>Java</div>
                    <div onClick={() => { this.onSwitch('CSS') }} style={{ cursor: 'pointer', margin: '0 20px', color: infinitKey === 'CSS' ? 'red' : '' }}>CSS</div>
                </div>
                <InfiniteScroll
                    initialLoad={false} // 
                    pageStart={1} // 
                    loadMore={this.loadMoreData}  // ajax
                    hasMore={hasMore} //  true  | false 
                    useWindow={true} //  window 
                    key={infinitKey}
                    style={{ width: '100%', overflow: 'hidden' }}
                >
                    <div className='row'>
                        {list.map((item, index) => {
                            return (
                                <div className="col-lg-3 col-sm-12 col-md-6 inner" key={index} >
                                    <div style={{ ...styles.item,padding: '20px' }}>
                                        <h2 style={{ textAlign: 'center' }}>#{index + 1}</h2>
                                        <div style={{ textAlign: 'center' }}>
                                            <img src={item.owner.avatar_url} style={{ width: '150px' }} />
                                        </div>
                                        <h3 style={{ textAlign: 'center', color: '#b83617',margin: '15px 0' }}><a style={{ color: 'red' }} href={item.owner.html_url}>{item.owner.login}</a></h3>
                                        <p style={{ paddingLeft: '20px',margin: '5px 0' }}><i className="fa fa-user-circle-o" style={{ marginRight: '10px',width: '16px',textAlign: 'center' }}></i><a style={{ color: '#000' }} href={item.owner.html_url}>{item.owner.login}</a></p>
                                        <p style={{ paddingLeft: '20px',margin: '5px 0' }}><i className="fa fa-star" style={{ marginRight: '10px',width: '16px' ,textAlign: 'center'}}></i>{item.stargazers_count}<span style={{ marginLeft: '10px' }}>stars</span></p>
                                        <p style={{ paddingLeft: '20px',margin: '5px 0' }}><i className="fa fa-code-fork" style={{ marginRight: '10px',width: '16px' ,textAlign: 'center'}}></i>{item.forks_count}<span style={{ marginLeft: '10px' }}>forks</span></p>
                                        <p style={{ paddingLeft: '20px',margin: '5px 0' }}><i className="fa fa-warning" style={{ marginRight: '10px',width: '16px',textAlign: 'center' }}></i>{item.open_issues_count}<span style={{ marginLeft: '10px' }}>open issues</span></p>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                </InfiniteScroll>
                <div style={{ display: this.state.isLoading ? 'block' : 'none',
                 textAlign: 'center',
                 width: '100vw',
                 height:'100vh',
                 position:'fixed',
                 left:'0',
                 top:'0',
                 background: 'rgba(0,0,0,.3)' }}>
                     <div style={{position:'absolute',
                     lineHeight:'40px',
                     color: '#fff',
                     top:'50%',
                     marginTop:'-20px',
                     textAlign: 'center',
                     fontSize:'30px',
                     fontWeight:'bold',
                     width: '100%'
                     }}>
                         <i className=" fa fa-spinner fa-spin"></i>
                         
                         </div>
                 </div>
            </div>
        );
    }
}
const styles = {
    nav: {
        display: 'flex',
        justifyContent: 'center',
        fontSize: '20px',
        fontWeight: 'bold',
        margin: '10px 0'
    },
    item: {
        backgroundColor: '#ebebeb',
        width: '100%',
        marginBottom: '10px',
        borderRadius: '10px'
    }
}

export default App;